/* === –û–±—â–∏–µ —Å—Ç–∏–ª–∏ –¥–ª—è body === */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  background-color: #121212;
  color: #ffffff;
  overflow: hidden;
  height: 100vh;
}

/* === –û–±—ë—Ä—Ç–∫–∞ –≤—Å–µ–≥–æ –∫–∞–ª–µ–Ω–¥–∞—Ä—è === */
.main-container {
  height: 100vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

/* === –ó–∞–≥–æ–ª–æ–≤–æ–∫ –∫–∞–ª–µ–Ω–¥–∞—Ä—è === */
.calendar-header {
  padding: 12px 20px 6px;
  border-bottom: 1px solid #222;
}

.calendar-header h2 {
  font-size: 22px;
  font-weight: 800;
  margin: 0;
}

.calendar-header p {
  font-size: 14px;
  color: #999;
  margin-top: 4px;
}

/* === –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä —Å –Ω–µ–¥–µ–ª—è–º–∏ === */
.week-container {
  flex-grow: 1;
  position: relative;
  overflow: hidden;
  padding: 8px 20px 16px;
}

/* === –ö–∞—Ä—Ç–æ—á–∫–∞ –Ω–µ–¥–µ–ª–∏ === */
.week-block {
  position: absolute;
  width: 92%;
  top: 10px;
  left: 20px;
  display: flex;
  flex-direction: column;
  background: #1f1f1f;
  border-radius: 20px;
  padding: 0 0 12px 0;
  box-shadow: 0 4px 15px #000000cc;
  box-sizing: border-box;
  overflow: hidden;
  position: relative;
}

/* –í–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–∞—è –ª–∏–Ω–∏—è */
.week-block::before {
  content: '';
  position: absolute;
  left: 76px;   /* 70 (day-left width) + 6 (padding-left) */
  top: 0px;    /* –æ—Ç—Å—Ç—É–ø —Å–≤–µ—Ä—Ö—É */
  bottom: 20px; /* –æ—Ç—Å—Ç—É–ø —Å–Ω–∏–∑—É */
  width: 1px;
  background-color: #444;
  border-radius: 2px;
  z-index: 1;
}

/* === –û–¥–∏–Ω –¥–µ–Ω—å –Ω–µ–¥–µ–ª–∏ === */
.day-row {
  position: relative;
  margin: 0 0 8px 0;
  padding: 10px 16px 10px 12px; /* –≤–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–æ –ø–æ 10px */
  border-radius: 12px;
  min-height: 48px; /* —Ñ–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–∞—è –≤—ã—Å–æ—Ç–∞ */
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
  box-sizing: border-box;
}

/* –ì–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω–∞—è –ª–∏–Ω–∏—è */
.day-row::after {
  content: '';
  position: absolute;
  left: 76px;  /* —Å–æ–≤–ø–∞–¥–∞–µ—Ç —Å –≤–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–æ–π –ª–∏–Ω–∏–µ–π */
  right: 0px; /* –æ—Ç—Å—Ç—É–ø —Å–ø—Ä–∞–≤–∞ —Å–æ–≤–ø–∞–¥–∞–µ—Ç —Å –ø–∞–¥–¥–∏–Ω–≥–æ–º */
  bottom: 0;
  height: 1px;
  background-color: #444;
  border-radius: 1px;
  z-index: 1;
}

/* –õ–µ–≤–∞—è —á–∞—Å—Ç—å —Å –¥–Ω–µ–º –∏ –¥–∞—Ç–æ–π */
.day-left {
  flex: 0 0 45px; /* —Ñ–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–∞—è —à–∏—Ä–∏–Ω–∞ */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2px;
  user-select: none;
  padding-left: 6px;
  box-sizing: border-box;
}

/* === –ù–∞–∑–≤–∞–Ω–∏–µ –¥–Ω—è === */
.day-name {
  color: #888;           /* –°–¥–µ–ª–∞–ª–∏ —Ü–≤–µ—Ç –ø–æ—Ç–µ–º–Ω–µ–µ –∏ –º–µ–Ω–µ–µ —è—Ä–∫–∏–º */
  font-weight: 600;
  font-size: 10px;       /* –£–º–µ–Ω—å—à–∏–ª–∏ —Ä–∞–∑–º–µ—Ä —Å 14px –¥–æ 12px */
  line-height: 1.2;
  padding: 0;
  text-align: center;
  user-select: none;
}

/* === –î–∞—Ç–∞ –ø–æ–¥ –Ω–∞–∑–≤–∞–Ω–∏–µ–º === */
.day-date {
  font-size: 13px;
  color: #ddd;
  line-height: 1.2;
  padding: 0px 6px;
  text-align: center;
}

/* === –í—ã–¥–µ–ª–µ–Ω–∏–µ —Å–µ–≥–æ–¥–Ω—è—à–Ω–µ–≥–æ –¥–Ω—è === */
.current-day .day-date {
  border: 1px solid #fff;
  background-color: #fff;
  border-radius: 50px;
  font-weight: 700;
  color: #000;
  padding: 2px 10px;
}

/* –ü–æ–ª–µ —Å–º–µ–Ω –∏ —Å–æ–±—ã—Ç–∏–π */
.day-events {
  flex-grow: 1;
  margin-left: 12px;
  color: #777;
  font-size: 13px;
  font-style: italic;
  user-select: none;
  min-height: 24px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
